@import '../common/variables.scss';

.advantages {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(./../images/fon.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: auto;
}

.advantages__wrapper {
  width: 87%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 25%;
}

.advantages__items {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages__description {
  text-align: center;
  margin-top: -2%;
  font-size: 14px;
}

.advantages__title {
  font-size: 18px;
  font-weight: bold;
}

.advantages__item--three::after {
  content: '';
  background-image: url("./../images/iphone.png");
  background-size: 157px auto;
  height: 275px;
  width: 160px;
  background-repeat: no-repeat;

  @media (min-resolution: $retina-dpi),
        (min-resolution: $retina-dppx) {
      background-image: url("./../images/iphone@2x.png");
  }
}

@media (min-width: $tablet-width) {
  .advantages {
    position: relative;
  }

  .advantages__wrapper {
    width: 94%;
    padding-top: 10%;
    padding-bottom: 10%;
    align-items: flex-start;
  }

  .advantages__item--three::after {
    position: absolute;
    bottom: 0;
    left: 3%;
    height: 480px;
    width: 268px;
    background-size: 268px auto;
  }

  .advantages__items {
    align-items: flex-start;
    padding: 0 4% 2% 50%;
  }

  .advantages__item {
    align-items: flex-start;
  }

  .advantages__description {
    text-align: left;
  }
}

@media (min-width: $desktop-width) {

  .advantages {
    background-size: cover;
  }

  .advantages__wrapper {
    width: 80%;
    padding-top: 2%;
    align-items: center;
  }

  .advantages__items {
    flex-direction: row;
    align-items: center;
    padding: 0;
  }

  .advantages__item {
    position: relative;
    align-items: center;
    font-size: 30px;
    line-height: 1;
    text-align: center;
    width: 100%;
    padding: 0 5%;
  }

  .advantages__item--three::after {
    display: none;
  }

  .advantages__title {
    font-size: 30px;
  }

  .advantages__description {
    text-align: center;
    font-size: 18px;
    line-height: 30px;
  }

  .advantages__item--first {
    &::before {
      content: '';
      background-image: url("./../images/iphone1.png");
      background-repeat: no-repeat;
      width: 100%;
      height: 300px;
      background-position: center;
    }
  }

  .advantages__item--second {
    &::before {
      content: '';
      background-image: url("./../images/iphone2.png");
      background-repeat: no-repeat;
      width: 100%;
      height: 300px;
      background-position: center;
    }
  }

  .advantages__item--three {
    &::before {
      content: '';
      background-image: url("./../images/iphone3.png");
      background-repeat: no-repeat;
      width: 100%;
      height: 300px;
      background-position: center;
    }
  }
}